import React from 'react'
import Message from "./Message"

const messageData = {
    avatar: 'https://sun9-74.userapi.com/Ph-WiuOtF985il9AvN9JqiCWedmHtSGSSTXrSA/ltEB2Z2-YO4.jpg',
    name: 'Some Name Some Name Some Name Some Name ',   //for text blur demonstration
    message: 'some text',
    time: '22:00',
}

function HW1() {
    return (
        <div style={{backgroundColor: "#a8e700"}}>
            <hr/>
            <h3>homeworks 1</h3>

            <Message
                avatar={messageData.avatar}
                name={messageData.name}
                message={messageData.message}
                time={messageData.time}
            />

            <hr/>
            {/*  ,  */}
            {/*<AlternativeMessage/>*/}
            <hr/>
        </div>
    )
}

export default HW1
